.menu {
  display: block;
  float: left;
  margin: 30px 0 0;
}

.menu__item {
  display: inline-block;
  vertical-align: middle;
  line-height: 30px;
  margin-right: 30px;
}

.menu__item__link {
  @include transition(.4s linear all);

  &:hover {
    color: #fff;
  }
}


